* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 0;
}

/* .country-list {
  display: flex;
  align-items: center;
} */
.country-list__name {
  display: inline-block;
  margin-left: 20px;
  font-size: 18px;
}
.country-info__list {
  list-style: none;
  padding: 0;
  font-size: 20px;
}
.country-info__img {
  margin-right: 10px;
}
.country-info__item:not(:last-child) {
  margin-bottom: 15px;
}
.country-info__value {
  margin-left: 10px;
  font-weight: 400;
}
